
#note-list{max-width:300px;width:300px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;background-color:#252326
}
#list-header{padding:25px
}
#list-header input{margin-bottom:25px;width:100%;padding:5px;box-sizing:border-box;border-radius:5px;border:1px solid #1C343A;color:#252326;background-color:#13444D;box-shadow:inset 0px 0px 25px 0px rgba(0,0,0,0.75);transition:all .5s;font-weight:bold
}
#list-header input:focus{outline:none;box-shadow:none
}
#list-header input::-webkit-input-placeholder{color:#252326
}
#list-header input:-ms-input-placeholder{color:#252326
}
#list-header input::placeholder{color:#252326
}
h2{text-align:center;color:#13444D;margin:0;padding:25px
}
.container{-ms-flex:1 1 auto;flex:1 1 auto;padding:5px;white-space:nowrap
}
div[role=group]{display:table;width:100%;border-collapse:collapse
}
div[role=group] button{margin:0;padding:5px;display:table-cell;width:50%;box-sizing:border-box;border:1px solid #1C343A;color:#252326;background-color:#13444D;font-weight:bold
}
div[role=group] button.active{box-shadow:inset 0px 0px 25px 0px rgba(0,0,0,0.75)
}
div[role=group] button:focus{outline:none;color:#0b0b0b
}
div[role=group] button:hover{background-color:#092024;color:#3f3b41
}
div[role=group] button:first-child{border-top-left-radius:5px;border-bottom-left-radius:5px
}
div[role=group] button:not(:first-child){border-top-right-radius:5px;border-bottom-right-radius:5px
}
a{display:block;background-color:#4F784E;margin:5px;text-decoration:none;overflow:hidden;color:#13444D;border-radius:5px;transition:all .5s
}
a:hover{background-color:#3b593a;color:#1d6876
}
a.active{box-shadow:inset 0px 0px 25px 0px rgba(0,0,0,0.75);color:#092024
}
a h4{margin:0;padding:5px
}



